import ManagerDetails from "./managerDetails-model.js";
const managerDetails = new ManagerDetails();
export default {
	data() {
		return {
			options: {},
			info: {}
		};
	},
	onLoad(options) {
		const that = this
		that.options = options
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			that.info = JSON.parse(that.options.data);
			// console.log(that.info)
			that.getDetails(() => {
				callBack && callBack();
			})
		},
		// 详情加载
		getDetails(callBack) {
			const that = this
			managerDetails.getDetails({
				openid: that.userInfo.openid,
				id: that.info.id
			}, (res) => {
				console.log(res)
				if (res.code == 4000) {
					// info
					that.info = Object.assign(that.info, res.data);
					managerDetails.show_tips(res.explain)
				}
				callBack && callBack();
			})
		},
		// 关注
		attention(callBack) {
			const that = this
			managerDetails.runAttention({
				openid: that.userInfo.openid,
				id: that.info.id
			}, (res) => {
				console.log(res)
				if (res.code == 4000) {
					managerDetails.show_tips(res.explain)
					that.info.fans_state = 1
				} else {
					managerDetails.show_tips(res.explain)
				}
				// callBack && callBack();
			})
		},
		// 取消关注
		notAttention(callBack) {
			const that = this
			managerDetails.runNotAttention({
				openid: that.userInfo.openid,
				id: that.info.id
			}, (res) => {
				console.log(res)
				if (res.code == 4000) {
					managerDetails.show_tips(res.explain)
					that.info.fans_state = 0
				} else {
					managerDetails.show_tips(res.explain)
				}
				// callBack && callBack();
			})
		},
		// 咨询
		advisory() {
			const that = this
			let id = that.info.id
			managerDetails.navigate_to(`/pages/newsChat/newsChat?id=${id}`);
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		// let shareData = {
		// 	title: '',
		// 	path: `pages/index/index?${this.userInfo.id}`,
		// 	imageUrl: ''
		// }
		// return managerDetails.onShareAppMessage(shareData);
	}
}
